/* Joakim Rosqvist - Mittuniversitetet - 2019 */

html {
  scroll-behavior: smooth;
  font-family: "Acme", sans-serif;
}

body {
  color: $darkGray;
  color: white;
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
a {
  color: $darkGray;
  color: white;
  line-height: 150%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Baloo Bhai", cursive;
}

// Meny
#mainheader {
  height: 50px;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 999;
  background-color: $darkGray;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  h2 {
    line-height: 40px;
    font-style: italic;
    color: #fff;
    float: left;
    padding-left: 20px;
    font-size: 50px;
    color: white;
    font-family: "Long Cang", cursive;
    &:hover {
        cursor:pointer;
      }
  }
  #main-nav {
    float: right;
    a {
      text-decoration: none;
      font-weight: bold;
      font-size: 16px;
      padding: 12px 20px;
      line-height: 50px;
      color: white;
      font-family: "Baloo Bhai", cursive;
      // & Används för att stylea pseudo-klasser
      &:hover {
        background-color: $yellow;
      }
    }
  }
  ul li {
    float: left;
    display: block;
  }
}

// Döljer mobilikonen
#mobiler-container {
  display: none;
}
#mobile-menu {
  display: none;
}

// Container
#fullpage {
  height: 100vh;
}
// Varje sektion på sidan
#fullpage section {
  min-height: 100%;
  padding-bottom: 50px;
  scroll-snap-align: center;
  width: 100%;
  h2 {
    font-size: 200%;
    padding: 30px 0 15px 0;
    text-align: center;
  }
}
#fullpage section:after {
  clear: both;
  content: "";
  display: block;
}
// SVG-animationen
#svg-banner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: fill 5.5s ease forwards 1.5s;
}

#svg-banner path:nth-child(1) {
  stroke-dasharray: 674.190673828125;
  stroke-dashoffset: 674.190673828125;
  animation: animation-lines 2s ease forwards;
}
#svg-banner path:nth-child(2) {
  stroke-dasharray: 51.316009521484375;
  stroke-dashoffset: 51.316009521484375;
  animation: animation-lines 2s ease forwards 0.2s;
}
#svg-banner path:nth-child(3) {
  stroke-dasharray: 723.7188110351562;
  stroke-dashoffset: 723.7188110351562;
  animation: animation-lines 2s ease forwards 0.4s;
}
#svg-banner path:nth-child(4) {
  stroke-dasharray: 328.5534362792969;
  stroke-dashoffset: 328.5534362792969;
  animation: animation-lines 2s ease forwards 0.6s;
}
#svg-banner path:nth-child(5) {
  stroke-dasharray: 273.49859619140625;
  stroke-dashoffset: 273.49859619140625;
  animation: animation-lines 2s ease forwards 0.8s;
}
#svg-banner path:nth-child(6) {
  stroke-dasharray: 481.3717041015625;
  stroke-dashoffset: 481.3717041015625;
  animation: animation-lines 2s ease forwards 1s;
}
#svg-banner path:nth-child(7) {
  stroke-dasharray: 357.3472900390625;
  stroke-dashoffset: 357.3472900390625;
  animation: animation-lines 2s ease forwards 1.2s;
}
#svg-banner path:nth-child(8) {
  stroke-dasharray: 316.30413818359375;
  stroke-dashoffset: 316.30413818359375;
  animation: animation-lines 2s ease forwards 1.4s;
}
#svg-banner path:nth-child(9) {
  stroke-dasharray: 273.49871826171875;
  stroke-dashoffset: 273.49871826171875;
  animation: animation-lines 2s ease forwards 1.6s;
}
#svg-banner path:nth-child(10) {
  stroke-dasharray: 405.4247131347656;
  stroke-dashoffset: 405.4247131347656;
  animation: animation-lines 2s ease forwards 1.8s;
}

// Varannan sektion
#fullpage section:nth-child(odd) {
  background-color: $darkGray;
  color: white;
  h2 {
    color: white;
  }
}
// Varannan sektion
#fullpage section:nth-child(even) {
  background-color: white;
  h2 {
    color: $darkGray;
  }
}
@keyframes animation-lines {
  to {
    stroke-dashoffset: 0;
  }
}
@keyframes fill {
  from {
    fill: transparent;
  }
  to {
    fill: white;
  }
}
// Pilen nedåt
#pointer i {
  font-size: 50px;
  color: white;
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translate(-50%, -30px);
  animation: bounce 3s infinite linear forwards;
}
@keyframes bounce {
  0% {
    transform: translateY(0px);
  }
  20% {
    transform: translateY(20px);
  }
  30% {
    transform: translateY(15px);
  }
  40% {
    transform: translateY(20px);
  }
  60% {
    transform: translateY(15px);
  }
  70% {
    transform: translateY(10px);
  }
  80% {
    transform: translateY(5px);
  }
  90% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(0px);
  }
}
.section-container {
  max-width: 960px;
  margin: 0 auto;
  padding: 2.5%;
}

#home {
  background-image: url("background2.jpg");
  background-repeat: no-repeat, repeat;
  background-size: cover;
  h2 {
    color: white;
    padding: 100px 0 50px 0 !important;
  }
}

#education {
  .education-box {
    float: left;
    width: 26.333333333333%;
    padding: 2.5%;
    margin: 1%;
    background-color: $yellow;
    border-radius: 5px;
    min-height: 150px;
    box-shadow: 0 7px 14px rgba(171, 171, 171, 0.25), 0 5px 5px rgba(171, 171, 171, 0.22);
  }
}

#portfolio {
  .webpage-box {
    float: left;
    width: 26.333333333333%;
    padding: 2.5%;
    margin: 1%;
    background-color: $yellow;
    border-radius: 5px;
    min-height: 100px;
    box-shadow: 0 7px 14px rgba(171, 171, 171, 0.25), 0 5px 5px rgba(171, 171, 171, 0.22);
  }
}

#work {
  .job-box {
    float: left;
    width: 26.333333333333%;
    padding: 2.5%;
    margin: 1%;
    background-color: $yellow;
    border-radius: 5px;
    min-height: 100px;
    box-shadow: 0 7px 14px rgba(171, 171, 171, 0.25), 0 5px 5px rgba(171, 171, 171, 0.22);
  }
}

#contact {
  background-image: url("background2.jpg");
  background-repeat: no-repeat, repeat;
  background-size: cover;
  position: relative;
  // Pilen uppåt
  #up-pointer i {
    font-size: 50px;
    color: white;
    position: absolute;
    bottom: 30px;
    left: 50%;
    animation: bounce-up 3s infinite linear forwards;
  }
  h2 {
    padding: 20px 0 10px 0;
    color: $darkGray;
    color: white;
  }
}
#contact-form-div {
  width: 80%;
  background-color: $yellow;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  box-shadow: 0 14px 28px rgba(150, 65, 65, 0.25), 0 10px 10px rgba(123, 47, 47, 0.22);
  border-radius: 30px;
  margin: 5% 10% 2.5% 10%;
  h2 {
    //color: $darkGray!important;
    color: white;
  }
  #contact-form {
    width: 90%;
    margin: 1% 2.5%;
    padding: 1% 2.5%;
  }
}
input[type="text"],
textarea {
  width: 86%;
  padding: 2%;
  margin: 0 5%;
  margin-bottom: 10px;
  border-radius: 5px;
  border: none;
}
textarea {
  min-height: 100px;
  resize: vertical;
}
/* Knappar */
button {
  box-shadow: 0 7px 14px rgba(0, 0, 0, 0.25), 0 5px 5px rgba(0, 0, 0, 0.22);
  border: none;
  border-radius: 5px;
  padding: 15px;
  font-weight: bold;
}
.submit-button {
  width: 30%;
  margin: 20px 35%;
  background-color: $darkGray;
  color: white;
  box-shadow: 0 7px 14px rgba(0, 0, 0, 0.25), 0 5px 5px rgba(0, 0, 0, 0.22);
  border-radius: 5px;
  &:hover {
    cursor: pointer;
    background-color: black;
  }
}

@keyframes bounce-up {
  0% {
    transform: translateY(0px);
  }
  20% {
    transform: translateY(-20px);
  }
  30% {
    transform: translateY(-15px);
  }
  40% {
    transform: translateY(-20px);
  }
  60% {
    transform: translateY(-15px);
  }
  70% {
    transform: translateY(-10px);
  }
  80% {
    transform: translateY(-5px);
  }
  90% {
    transform: translateY(-5px);
  }
  100% {
    transform: translateY(0px);
  }
}

// MEDIA QUERIES
@media (max-width: 800px) {
  // Designar mobilmenyn
  #main-nav {
    z-index: 999;
    position: relative;
    float: none;
    width: 100%;
    a {
      padding: 5px;
      text-align: left;
      width: 100%;
      display: block;
      line-height: 0;
    }
    ul {
      display: none;
    }
    ul li {
      background-color: $darkGray;
      float: none;
      width: 100%;
      display: block;
      z-index: 99999;
    }
    ul li:nth-child(1) {
      border-top: 2px solid white;
    }
  }
  // Visar menyn då den har klassen open (via klick)
  #main-nav ul.open {
    display: block;
  }
  #mobile-container {
    position: relative;
    width: 40px;
    float: right;
    margin: 16px 10px;
  }
  #mobile-menu {
    cursor: pointer;
    display: block;
  }
  // De tre strecken som bygger upp mobilmenyn
  #mobile-menu span {
    background: white;
    display: block;
    width: 25px;
    height: 3px;
    margin-bottom: 3px;
    position: relative;
    top: 0;
    transition: all ease-in-out 0.2s;
    border-radius: 5px;
  }
  // Mittenstrecket försvinner vid klick
  #mobile-menu.open span:nth-child(2) {
    width: 0;
    opacity: 0;
  }
  // Det understa strecket roterar och bildar ett halvt kryss
  #mobile-menu.open span:nth-child(3) {
    transform: rotate(45deg);
    top: -6px;
  }
  // Det översta strecket roterar och bildar ett halvt krytt
  #mobile-menu.open span:nth-child(1) {
    transform: rotate(-45deg);
    top: 6px;
  }
  // SVG-animationen
  #svg-banner {
    width: 75%;
    height: 75%;
  }
  #work .job-box,
  #education .education-box,
  #portfolio .webpage-box {
    width: 43%;
    padding: 2.5%;
    margin: 1%;
  }
  #contact-form-div {
    width: 90%;
    margin: 5% 5% 2.5% 5%;
    #contact-form {
      width: 90%;
      margin: 1% 2.5%;
      padding: 1% 2.5%;
    }
  }
  
}

@media (max-width: 550px) {
  // SVG-animationen
  #svg-banner {
    width: 50%;
    height: 50%;
  }
  #work .job-box,
  #education .education-box,
  #portfolio .webpage-box {
    width: 93%;
    padding: 2.5%;
    margin: 1%;
  }
  #contact-form-div {
    width: 95%;
    margin: 5% 2.5% 2.5% 2.5%;
    #contact-form {
      width: 92%;
      margin: 1% 1.5%;
      padding: 1% 2.5%;
    }
  }
  input[type="text"],
  textarea {
    width: 91%;
    margin: 0 2.5%;
    margin-bottom: 10px;
  }
  .submit-button {
    width: 40%;
    margin: 20px 30%;

}
}
